:root {
    --luminacious-fs-1: 160%; //240%; //250%;
    --luminacious-fs-2: 130%; //170%;
    --luminacious-fs-3: 100%;
    --luminacious-fs-4: 80%;
    --luminacious-fs-5: 60%;
    --luminacious-fs-6: 50%;
}

.markdown-content {
    font-size: 80%;
    h1, h2, h3, h4, p {
        margin: 1rem 0;
    }
}

h1, h2, h3, h4, h5, h6 {font-weight: bold;}

.markdown-content > h1, .fs-1 {font-size: var(--luminacious-fs-1)!important;}
.markdown-content > h2, .fs-2 {font-size: var(--luminacious-fs-2)!important;}
.markdown-content > h3, .fs-3 {font-size: var(--luminacious-fs-3)!important;}
.markdown-content > h4, .fs-4 {font-size: var(--luminacious-fs-4)!important;}
.markdown-content > h5, .fs-5 {font-size: var(--luminacious-fs-5)!important;}
.markdown-content > h6, .fs-6 {font-size: var(--luminacious-fs-6)!important;}

@media (min-width: 576px) {
    .markdown-content {font-size: 100%;}
    .fs-sm-1 {font-size: var(--luminacious-fs-1)!important;}
    .fs-sm-2 {font-size: var(--luminacious-fs-2)!important;}
    .fs-sm-3 {font-size: var(--luminacious-fs-3)!important;}
    .fs-sm-4 {font-size: var(--luminacious-fs-4)!important;}
    .fs-sm-5 {font-size: var(--luminacious-fs-5)!important;}
    .fs-sm-6 {font-size: var(--luminacious-fs-6)!important;}
}

@media (min-width: 768px) {
    .fs-md-1 {font-size: var(--luminacious-fs-1)!important;}
    .fs-md-2 {font-size: var(--luminacious-fs-2)!important;}
    .fs-md-3 {font-size: var(--luminacious-fs-3)!important;}
    .fs-md-4 {font-size: var(--luminacious-fs-4)!important;}
    .fs-md-5 {font-size: var(--luminacious-fs-5)!important;}
    .fs-md-6 {font-size: var(--luminacious-fs-6)!important;}
}

@media (min-width: 992px) {
    .fs-lg-1 {font-size: var(--luminacious-fs-1)!important;}
    .fs-lg-2 {font-size: var(--luminacious-fs-2)!important;}
    .fs-lg-3 {font-size: var(--luminacious-fs-3)!important;}
    .fs-lg-4 {font-size: var(--luminacious-fs-4)!important;}
    .fs-lg-5 {font-size: var(--luminacious-fs-5)!important;}
    .fs-lg-6 {font-size: var(--luminacious-fs-6)!important;}
}

@media (min-width: 1200px) {
    .fs-xl-1 {font-size: var(--luminacious-fs-1)!important;}
    .fs-xl-2 {font-size: var(--luminacious-fs-2)!important;}
    .fs-xl-3 {font-size: var(--luminacious-fs-3)!important;}
    .fs-xl-4 {font-size: var(--luminacious-fs-4)!important;}
    .fs-xl-5 {font-size: var(--luminacious-fs-5)!important;}
    .fs-xl-6 {font-size: var(--luminacious-fs-6)!important;}
}

@media (min-width: 1400px) {
    .fs-xxl-1 {font-size: var(--luminacious-fs-1)!important;}
    .fs-xxl-2 {font-size: var(--luminacious-fs-2)!important;}
    .fs-xxl-3 {font-size: var(--luminacious-fs-3)!important;}
